import React, { useEffect, useState } from "react";
import "./view.css";

const ViewRanking = () => {
  const [rankings, setRankings] = useState([]);

  useEffect(() => {
    fetch("http://localhost:5500/api/rankings")
      .then((res) => res.json())
      .then((data) => setRankings(data))
      .catch((err) => console.log(err));
  }, []);

  return (
    <div className="ranking-wrap">
      <h1 className="ranking-title">Community Rankings</h1>
      <p className="ranking-subtitle">
        See whoâ€™s leading the EmiBot community this month!
      </p>

      <div className="ranking-list">
        {rankings.map((user) => (
          <div key={user.rank} className={`ranking-card rank-${user.rank}`}>
            <div className="rank-badge">{user.rank}</div>
            <img src={user.image} alt={user.name} className="rank-image" />
            <div className="rank-info">
              <h3>{user.name}</h3>
              <p>{user.points} points</p>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default ViewRanking;
